import { randomString } from "@/utils";
import meSideMenu from "@/components/meSideMenu";
import { MY_JOBS, SET_READ, USER_UNREAD } from '@/api/api';
export default {
  name: "jobs",
  components: {
    meSideMenu
  },

  data() {
    return {
      userInfo: {},
      basicUserInfo: {},
      jobListData: [],
      jobPage: 1,
      jobLimit: 5,
      jobTotalNum: 0,
      versionTime: randomString()
    };
  },

  mounted() {
    this.getMyJobs(this.jobPage, this.jobLimit);
  },

  methods: {
    postJob() {
      this.$router.push({
        path: '/jobs/post',
        query: {
          version_time: this.versionTime
        }
      });
    },

    jobPageSizeChange(e) {
      console.log(e);
    },

    jobPageChange(e) {
      this.jobPage = e;
      this.getMyJobs(e, this.jobLimit);
      console.log(e);
    },

    getMyJobs(page, limit) {
      let params = {
        token: localStorage.getItem('token'),
        page: page,
        limit: limit
      };
      MY_JOBS(params).then(res => {
        console.log(res);

        if (res.code == 200) {
          let jobData = res.message.data;
          this.jobTotalNum = res.message.total;
          let unread_data = {
            identity: localStorage.getItem('identity'),
            token: localStorage.getItem('token')
          };
          USER_UNREAD(unread_data).then(res => {
            if (res.code == 200) {
              let unreadListData = res.message.list;
              jobData.forEach(item => {
                // console.log(item)
                let a = unreadListData.filter(function (element) {
                  return element.type == 1 && element.type_id == item.id;
                });

                if (a.length > 0) {
                  item.unread_status = true;
                  item.unread_id = a[0].id;
                } else {
                  item.unread_status = false;
                }
              });
              this.jobListData = jobData;
              console.log(jobData);
            } else {
              console.log('unread:' + res.msg);
            }
          });
        }
      }).catch(err => {
        console.log(err);
        this.$message.error(err.msg);
      });
    },

    turnApplications(id, unreadId) {
      let data = {
        id: unreadId,
        identity: localStorage.getItem('identity'),
        status: 1,
        token: localStorage.getItem('token')
      };
      SET_READ(data).then(res => {
        console.log(res);

        if (res.code == 200) {
          this.$router.push({
            path: '/jobs/applications',
            query: {
              id: id
            }
          });
        } else {
          console.log('set read:' + res.msg);
        }
      }).catch(err => {
        console.log(err);
      });
    },

    turnEditJobs(jobId) {
      this.$router.push({
        path: '/jobs/post',
        query: {
          job_id: jobId
        }
      });
    }

  }
};